import Vue from 'vue'
import Router from 'vue-router'
import Overview from '@/components/Overview'
import Home from '@/components/Home'
import CreateLove from '@/components/CreateLove'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Overview',
      redirect: '/home',
      component: Overview,
      children: [{
        path: '/home',
        name: 'Home',
        component: Home
      }, {
        path: '/createLove',
        name: 'CreateLove',
        component: CreateLove
      }]
    }
  ]
})



